import {Box, Grid, Paper, Stack, Typography} from "@mui/material";

function Career() {
    const SectionTitle = (props) => {
        return (<Typography
                fontSize={'h4.fontSize'}
                fontWeight={'bolder'}
                color='background.default'
                margin={12}
            >
                {props.text}
            </Typography>);
    }

    const sectionBoxSx = (color) => {
        return ({
                backgroundColor: color, justifyContent: 'center', height: '100%', width: '100%', display: 'block'
            })
    }

    const sectionCard = (cardTitle, cardText, cardImg) => {
        return (<Grid item xs={12} md={12} lg={6}>
                <Paper sx={paperSx} elevation={4}>
                    <Grid container justifyContent={'space-between'}>
                        <Grid
                            item
                            component={'img'}
                            src={cardImg}
                            xs={6}
                        />
                        <Grid item xs={6} p={2} justifyContent={'flex-start'}>
                            <Typography variant={'h6'} fontWeight={'bold'} marginBottom={3}>{cardTitle}</Typography>
                            <Typography variant={'body1'} align="left">{cardText}</Typography>
                        </Grid>
                    </Grid>
                </Paper>
            </Grid>)
    }

    const paperSx = {
        borderRadius: 8,
        overflow: 'hidden',
        marginBottom: 10,
        elevation: 2,
        transition: 'all 0.25s ease-in-out',
        '&:hover': {
            transform: 'scale(1.1)',
        }
    }

    return (<Stack spacing={0} justifyContent={'center'}>
            <Box sx={sectionBoxSx('primary.dark')}>
                <SectionTitle text="팀과 함께라면 더 많은 일을 할 수 있습니다."/>
                <Grid container p={'10%'} justifyContent={'flex-around'} columnSpacing={12}>
                    {sectionCard("K-사이버시큐리티챌린지",
                        "K-사이버시큐리티 AI 분야 과학기술부 장관상. 지금은 익숙하지만," +
                        " 거의 처음으로 팀으로써 프로젝트를 진행하고 큰 성과를 거두었습니다. " +
                        "Random Forest, LightGBM과 같은 간단한 머신러닝 모델부터 Malconv, M-CNN, Invincea와 같은 논문 구현까지," +
                        "단순 이진 파일에서 데이터의 특성을 읽어내야 하는 어려운 작업이였으나, " +
                        "프로젝트 회의 때 의견 충돌이 발생하기도 하고 서로 크고 작은 실수가 있었지만, " +
                        "비난하기보다는 서로 돕고 대립하기보다는 서로 조율하는 덕에 정말 좋은 실적을 거둘 수 있습니다.",
                        "kcyber.jpg")}

                    {sectionCard("HaiCon2020",
                        "HAICON 2020 산업제어시스템 보안위협 탐지대회 장려상." +
                        "처음으로 대용량 시계열 데이터에 대한 분석을 진행했던 프로젝트였으며, " +
                        "시계열 데이터 및 인공지능 모델 구조에 대하여 많은 공부를 할 수 있었던 기회였습니다." +
                        " LSTM VAE, GRU AE, DeepSVDD와 같은 기술과 논문을 구현하였으며, " +
                        "이때 공부하고 개발되었던 기술을 토대로 2020년에 코로나 확진자 예측 모델을 만들었었는데, " +
                        "9월의 확진자 급증을 놀랍게도 어느 정도 예측하였습니다.",
                        "hai.png")}
                </Grid>
            </Box>

            <Box sx={sectionBoxSx('secondary.dark')}>
                <SectionTitle text={'세계로 나아갈 준비가 되었습니다.'}/>
                <Grid container p={'10%'} justifyContent={'flex-around'} columnSpacing={12}>
                    {sectionCard("Research With UC Irvine",
                        "스마트 기기의 센서에서 보내는 데이터를 가공. 이를 통한 감정 분석 및 예측. " +
                        "스마트 기기들은 아마 우리보다 우리를 더 잘 알지도 모릅니다. 하루종일 우리와 함께하며 눈, 코, 귀" +
                        "와 같은 센서들을 통해 끊임없이 우리와 소통하기 때문이지요. 이런 정보를 이용하면, 우리는 스스로를 좀 더 잘" +
                        " 파악할 수 있게 될 것이고, 나아가 사람끼리도 서로 좀 더 잘 이해할 수 있게 될 것입니다.",
                        "uci.png")}

                    {sectionCard("KATUSA",
                        "Senior KATUSA, 2 Infantry Division 여단장 표창, 한국군지원단장 표창. 운이 좋아서 카투사에 합격할 수 있었으며" +
                        " 그에 감사하며 그저 열심히 군 생활을 했었던 것 같습니다. 미국의 문화를 경험했다고는 할 수 없지만, 우리나라가" +
                        " 아닌 다른 나라의 사람들과 깊게 교류하고 소통할 수 있는 소중한 기회였으며, 결국 사람은 다 비슷하다는 것을 느낄 수 " +
                        "있는 계기가 되었습니다.",
                        "katusa.jpg")}
                </Grid>
            </Box>

            <Box sx={sectionBoxSx('success.dark')}>
                <SectionTitle text={'늘 세상을 위해 고민합니다.'}/>
                <Grid container p={'10%'} justifyContent={'flex-around'} columnSpacing={12}>
                    {sectionCard("문서형 악성코드 탐지 모델",
                        "정확도 99%. 제가 만든 PDF 파일의 통계 정보를 이용해 일반화된 특징 벡터를 이용하여 만든 PDF 악성코드의 탐지 성능입니다." +
                        "문서형 악성코드를 연구하던 도중 국내 백신의 탐지 성능이 외국의 기업에 비하여 좋지 않은 걸 알았을 때에 어떻게 하면 가볍고 빠르게 악성코드를 걸러낼 수 있을까 고민하여 만들어낸 결과입니다.",
                        "pdfmal.png")}

                    {sectionCard("독거노인을 위한 우울감 진단 모델",
                        "사람보다 더 높은 정확도로 우울감을 탐지하는 NLU, 이를 탐지하기 위하여 사용자와 대화를 시도하는" +
                        " NLG 모델을 개발하였습니다." +
                        "혼자 사시는 분들의 우울감이 일정 수준 이상에 도달하면, 회원가입시에 등록된 관리자나 가족에게 즉시 통보됩니다." +
                        "노인의 50% 가까이 차지하는 독거노인과, 이분들 중 30%가 현재 우울하다고 답하셨습니다." +
                        "이는 결고 가벼운 문제가 아니며, 저절로 해결되는 문제도 아닙니다.",
                        "aid.jpg")}
                </Grid>
            </Box>
        </Stack>);

}

export default Career;